html,body,
div,span,p,a,
applet,
object,
iframe,
h1,h2,h3,h4,h5,h6,
blockquote,
pre,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,u,i,center,
dl,dt,dd,ol,ul,li,
fieldset,form,label,legend,
caption,table,tr,th,td,thead,tbody,tfoot,
article,aside,canvas,details,embed,
figure,figcaption,footer,header,hgroup,
menu,nav,
output,
ruby,
section,article,
summary,
time,
mark,
audio,video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
header,footer,section,article {}
aside,
details,
figcaption,
figure,
hgroup,
menu,nav {
	display: block; }

body {
	line-height: 1;
	font-family: 'Montserrat', sans-serif;
	font-size: 15px;
	font-weight: 500; }

blockquote,
q {
	quotes: none; }

ol,ul {
	list-style: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
	content: '';
	content: none; }

table {
	border-collapse: collapse;
	border-spacing: 0; }

a,
a:visited {
	color: #000;
	text-decoration: none;
	-webkit-transition: all .4s ease-in-out;
	transition: all .4s ease-in-out; }

a:hover,
a:visited:hover {
	@media(hover) {
		color: #bbb;
		text-decoration: none; } }

a:focus {
	outline: 0; }

input {
	outline: none; }

* {
	-webkit-transition: all .4s ease-in-out;
	transition: all .4s ease-in-out; }

body {
	position: relative; }

@keyframes fadeIn {
	from {
		opacity: 0; }
	to {
		opacity: 1; } }
.fadeIn {
	animation-name: fadeIn; }

@keyframes fadeOut {
	from {
		opacity: 1; }
	to {
		opacity: 0; } }
.fadeOut {
	animation-name: fadeOut; }

.overflow-hidden {
	overflow: hidden; }

h2 {
	position: relative;
	color: #95B73C;
	font-size: 27px;
	font-family: Lora;
	font-weight: 700;
	padding-top: 24px;
	width: max-content;
	width: -moz-max-content;
	max-width: 80%;
	margin: auto;
	&:before {
		position: absolute;
		content: '';
		top: -2.5px;
		width: 100%;
		height: 5px;
		background-color: #95B73C; } }

.header {
	z-index: 1000;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	.contacts {
		display: flex;
		align-items: center;
		position: relative;
		height: 53px;
		background-color: #95B73C;
		.content {
			display: flex;
			align-items: center;
			justify-content: flex-end;
			color: #fff;
			font-size: 13px;
			.adress, .tel {
				position: relative;
				display: flex;
				align-items: center;
				min-height: 30px;
				margin-right: 70px;
				font-weight: 500; }
			.adress {
				padding-left: 25px;
				background-image: url('../img/ico-geo.svg');
				background-repeat: no-repeat;
				background-size: 17px 23px;
				background-position: left center;
				@media screen and (max-width: 991px) {
					margin-right: 30px; }
				// &:before
				// 	position: absolute
				// 	content: ''
				// 	left: 0
				// 	top: 0
				// 	height: 30px
				// 	width: 30px
				// 	border-radius: 50%
 }				// 	background-color: rgba(255,255,255,.6)
			.tel {
				position: relative;
				color: #fff;
				font-family: Montserrat;
				font-size: 17px;
				font-weight: 700;
				&:after {
					position: absolute;
					content: '';
					bottom: 0;
					left: 50%;
					width: 0%;
					height: 1px;
					background-color: #fff;
					transition: all .4s; }
				&:hover:after {
					@media(hover) {
						width: 100%;
						left: 0px; } } }
			.soc-list {
				display: flex;
				align-items: center;
