					width: 24px;
					&:not(:last-child) {
						margin-right: 16px; }
					&:hover {
						@media(hover) {
							transform: scale(1.2); } }
					a {
						display: block;
						img {
							height: 100%;
							width: 100%; } } } } } }
	.menu {
		// position: relative
		display: flex;
		align-items: center;
		height: 58px;
		background-color: rgba(99,51,28,.76);
		.container {
			height: 100%; }
		nav {
			height: 100%;
			position: relative;
			display: flex;
			align-items: center;
			justify-content: flex-end;
			.main-menu {
				display: flex;
				justify-content: flex-end;
				@media screen and (max-width: 991px) {
					flex-flow: column;
					height: 100vh;
					background-color: rgba(99, 51, 28, 0.95);
					position: fixed;
					top: 0;
					width: 40%;
					align-items: center;
					justify-content: flex-start;
					right: -40%;
					z-index: 1500;
					overflow: scroll;
					padding: 80px 0 40px; }
				@media screen and (max-width: 767px) {
					width: 50%;
					right: -50%; }
				@media screen and (max-width: 575px) {
					width: 80%;
					right: -80%; }
				&.active {
					right: 0; }
				.close-button {
					width: 28px;
					position: absolute;
					top: 80px;
					right: 40px;
					@media screen and (max-width: 575px) {
						z-index: 1000;
						right: 10px; }
					span {
						display: block;
						height: 3px;
						width: 100%;
						background-color: #fff;
						border-radius: 2px;
						transform: rotate(45deg);
						&:after {
							content: '';
							height: 3px;
							width: 100%;
							background-color: #fff;
							transform: rotate(-90deg);
							top: 0px;
							position: absolute;
							width: 100%;
							left: 0px; } } }
				li {
					&:not(:last-of-type) {
						margin-right: 40px;
						@media screen and (max-width: 991px) {
							margin-right: 0;
							margin-bottom: 30px; } }
					a {
						position: relative;
						color: #fff;
						font-size: 13px;
						font-weight: 500;
						text-decoration: none;
						@media screen and (max-width: 991px) {
							font-size: 16px; }
						&:after {
							position: absolute;
							content: '';
							bottom: -5px;
							left: 50%;
							width: 0%;
							height: 1px;
							background-color: #fff;
							transition: all .4s; }
						&:hover:after {
							@media(hover) {
								width: 100%;
								left: 0px; } } } }
				a.tel {
					display: block;
					margin-top: 80px;
					color: #fff;
					text-decoration: none; } } }
		.burger-menu {
			position: absolute;
			right: 0;
			height: 22px;
			width: 28px;
			top: 50%;
			margin-top: -11px;
			cursor: pointer;
			z-index: 500;
			@media screen and (max-width: 767px) {
				margin-top: -12px; }
			span {
				margin: auto 0;
				display: block;
				height: 3px;
				width: 100%;
				background-color: #fff;
				border-radius: 2px; }
			span:before {
				content: '';
				position: absolute;
				top: 50%;
				margin-top: -1px;
				height: 3px;
				width: 100%;
				background-color: #fff;
				border-radius: 2px;
				transition: all .3s; }
			span:after {
				content: '';
				position: absolute;
				bottom: 0;
				height: 3px;
				width: 100%;
				background-color: #fff;
				border-radius: 2px;
				transition: all .3s; }
			&.active {
				opacity: 0; } } }
	.logo-container {
		position: absolute;
		top: 15px;
		@media screen and (max-width: 767px) {
			top: 35px; }
		.logo-link {
			display: block;
			height: 141px;
			width: 140px;
			@media screen and (max-width: 1199px) {
				height: 120px;
				width: 120px; }
			@media screen and (max-width: 767px) {
				height: 100px;
				width: 100px; }
			img {
				height: 100%;
				width: 100%; } } } }
