.map-section {
	position: relative;
	padding: 110px 0px;
	@media screen and (max-width: 767px) {
		padding: 0;
		background-image: url('../img/forest.jpg');
		background-repeat: no-repeat;
		background-position: center bottom;
		background-size: auto 60%; }
	.map-container {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 100;
		width: 100%;
		height: 100%;
		background-color: #cecece;
		display: flex;
		align-items: center;
		justify-content: center;
		padding-left: 100px;
		@media screen and (max-width: 991px) {
			padding-left: 300px; }
		@media screen and (max-width: 767px) {
			position: relative;
			height: 400px;
			padding-left: 0; } }
	.map-section-form {
		position: relative;
		z-index: 200;
		padding: 35px 55px 50px;
		background-color: #D5BB9A;
		max-width: 380px;
		color: #fff;
		line-height: 1.2;
		@media screen and (max-width: 767px) {
			margin: auto; }
		@media screen and (max-width: 575px) {
			padding: 35px 25px 50px; }
		h3 {
			font-family: Lora;
			font-size: 20px;
			font-weight: 700;
			text-align: center;
			margin-bottom: 40px; }
		input {
			width: 100%;
			border: none;
			padding: 12px 18px 12px;
			height: 42px;
			margin-bottom: 30px;
			&.tel-input {
				margin-bottom: 12px; }
			&::placeholder {
				color: #BF9766; } }
		.info {
			font-size: 13px;
			margin-bottom: 40px; }
		.submit-button {
			display: block;
			min-height: 42px;
			max-width: 195px;
			border: none;
			padding: 12px 35px;
			margin-left: auto;
			background-color: #94B63B;
			color: #fff;
			&:hover {}
			@media(hover) {
					box-shadow: inset 0px 0px 100px rgba(0,0,0,.3);
					cursor: pointer; } } } }
.forest {
	height: 300px;
	width: 100%;
	background-image: url('../img/forest.jpg');
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	@media screen and (max-width: 767px) {
		display: none; } }
.footer {
	background-color: #401D0E;
	padding: 38px 0 60px;
	.footer-content {
		display: flex;
		align-items: center;
		justify-content: space-between;
		@media screen and (max-width: 991px) {
			flex-flow: column; } }
	.logo-container {
		display: inline-flex;
		align-items: center;
		padding-left: 100px;
		background-image: url('../img/logo.png');
		background-position: left center;
		background-repeat: no-repeat;
		background-size: 80px;
		min-height: 80px;
		color: #FCA677;
		font-family: Lora;
		font-size: 27px;
		font-weight: 700;
		transition: all .4s;
		&:hover {
			@media(hover) {
				text-decoration: none; } }
		@media screen and (max-width: 575px) {
			background-size: 60px;
			min-height: 60px;
			padding-left: 80px;
			font-size: 24px; }
		&:hover {
			@media(hover) {
				color: #FCA677; } }
		@media screen and (max-width: 991px) {
			margin-bottom: 20px; } }
	.content {
		.soc-list {
			display: flex;
			align-items: center;
			justify-content: flex-end;
			.item {
				height: 24px;
				width: 24px;
				&:not(:last-child) {
					margin-right: 16px; }
				&:hover {
					@media(hover) {
						transform: scale(1.2); } } }
			@media screen and (max-width: 575px) {
				justify-content: center; } }
		.footer-info {
			display: flex;
			align-items: center;
			justify-content: flex-end;
			margin-bottom: 40px;
			@media screen and (max-width: 575px) {
				flex-flow: column; }
			.item {
				height: 30px;
				width: 202px;
				background-color: rgba(255,255,255,.61);
				border-radius: 4px;
				&:not(:last-child) {
					margin-right: 32px;
					@media screen and (max-width: 575px) {
						margin: auto auto 20px; } }
				@media screen and (max-width: 575px) {
					width: 200px; } } }
		.contact-row {
			margin-bottom: 30px;
			display: flex;
			align-items: center;
			justify-content: flex-end;
			color: #fff;
			@media screen and (max-width: 575px) {
				flex-flow: column; }
			.adress, .tel {
				position: relative;
				display: flex;
				align-items: center;
				min-height: 30px;
				padding-left: 40px;
				font-weight: 500;
				@media screen and (max-width: 575px) {
					padding-left: 0px; } }
			.adress {
				padding-left: 25px;
				background-image: url('../img/ico-geo.svg');
				background-repeat: no-repeat;
				background-size: 17px 23px;
				background-position: left center; }
			.tel {
				position: relative;
				color: #fff;
				font-family: Montserrat;
				font-size: 17px;
				font-weight: 700;
				margin-right: 70px;
				text-decoration: none;
				// @media(hover)
				@media screen and (max-width: 991px) {
					margin-right: 50px; }
				@media screen and (max-width: 575px) {
					margin: auto auto 10px; }
				&:after {
					position: absolute;
					content: '';
					bottom: 0;
					left: 50%;
					width: 0%;
					height: 1px;
					background-color: #fff;
					transition: all .4s; }
				&:hover:after {
					@media(hover) {
						width: calc(100% - 40px);
						left: 40px; } } } } } }
